.grid {
  display: grid;
  grid-template-columns: 2fr 3fr;
}
.logo {
  padding: 42px 0 0 38px;
}
.content {
  padding: 0 38px;
  margin: 83px 0 0 0;
  font-family: "Roboto", sans-serif;
  font-style: normal;
}
.main_heading {
  color: #7e53c8;
  font-size: 42px;
  font-weight: 700;
  line-height: 49px;
  letter-spacing: 0em;
}
.sub_heading {
  color: #858383;
  font-size: 18px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: left;
  text-transform: uppercase;
}
.des_heading {
  font-size: 17px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;
}
.btn-plan {
  color: #fff;
  width: 192px;
  height: 48px;
  background: #7e53c8;
  border: #7e53c8;
  border-radius: 12px;
  margin-top: 36px;
  margin-left: 38px;
}
.btn-plan:hover {
  cursor: pointer;
}

/* grid -item-2 */
.grid_item_2 {
  position: relative;
  height: 100vh;
}
.grid_item_2 img {
  height: 100%;
  width: 100%;
}
.bg_purple {
  height: 100%;
  width: 100%;
  background-color: #c55ffc;
}

/* Navbar */
.navbar {
  position: absolute;
  top: 40px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.navbar a {
  font-family: "Roboto", sans-serif;
  display: inline-block;
  text-decoration: none;
  margin: 20px;

  color: #fff;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: center;
}

@media (max-width: 600px) {
  .grid {
    grid-template-columns: 1fr;
  }
  .navbar {
    display: none;
  }
  .grid_item{
    height: 100vh;
  }
  .grid_item_2 {
    position: absolute;
    z-index: -2;
  }
  .content{
    height: 60vh;
    margin-top: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .content div > p ,.content >p {
    color: #ffff;
    margin-top: 0.7em;
    margin-bottom: 0.7em;
  }
  
  .des_heading{
    color: rgba(255, 255, 255, 0.7) !important;
    font-weight: bold;
  }
  .logo {
    padding-left: 0;
    display: flex;
    justify-content: center;
  }
  .btn-plan {
    color: black;
    background: #ffff;
    border: #ffff;
    font-family: Roboto;
    font-style: normal;
    font-weight: 600;
    font-size: 19px;
    line-height: 22px;
    text-align: center;
  }
}